
*{
  margin: 0;
  padding: 0;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
body {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #666;
  margin: 60px;
}
button {
  background-color: inherit;
  outline: none;
  border: none;
  cursor: pointer;
}
.just-btn {
  padding: 10px 20px;
  font-size: 18px;
  color: #666;
  background-color: #ddd;
  border: 2px solid #aaa;
  border-radius: 20px;
}

.contacts {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.users-list {
  text-align: right;
  -ms-flex-preferred-size: 40%;
      flex-basis: 40%;
}
.warning {
  margin-top: 20px;
}

.users-table {
  width: 100%;
  margin-top: 20px;
  text-align: left;
  border-top: 1px solid #666;
  border-bottom: 1px solid #666;
}
.users-table__body {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
}
.users-table__row {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: end;
      -ms-flex-pack: end;
          justify-content: flex-end;
  -ms-flex-preferred-size: content;
      flex-basis: content;
  border-bottom: 1px solid #666;
}
.users-table__row:last-child {
  border-bottom: none;
}
.users-table__cell {
  -ms-flex-preferred-size: 50%;
      flex-basis: 50%;
  padding: 20px;
  font-weight: normal;
  border-right: 1px solid #666;
  border-left: 1px solid #666;
}
.users-table__cell:last-child {
  border-left: none;
}

.users-table__header {
  border-bottom: 1px solid #666;
  cursor: pointer;
}

.users-table__button {
  position: relative;
  left: -10px;
  padding: 0 5px;
}
.subcell {
  -ms-flex-preferred-size: 40%;
      flex-basis: 40%;
}

.adding-widget {
  -ms-flex-preferred-size: 40%;
      flex-basis: 40%;
  margin-top: 65px;
  margin-left: 65px;
  padding: 20px;
  border: 1px solid #666;
}
.adding-widget__header {
  display: -webkit-box;
